@mixin phone {
        @media (max-width: 400px) { @content; }
    }
    @mixin tablet {
        @media (min-width: 400px) and (max-width: 768px) { @content; }
    }
    @mixin desktop-min {
        @media (min-width: 768px) and (max-width: 1024px) { @content; }
    }
    @mixin desktop {
        @media (min-width: 1024px) and (max-width: 1440px) { @content; }
    }
    @mixin big-desktop {
        @media (min-width: 1440px) { @content; }
}